<template>
    <div class="content public-offer">
      <div class="inner">
          <div v-html="content.description"></div>
          <div class="button-block mt32">
              <button class="btn" @click="closePublicOffer">
                  Принимаю!
              </button>
          </div>
        </div>
    </div>
</template>



<script>
    import { mapState } from 'vuex'
    export default {
        metaInfo() {
            return {
                title: 'Публичная оферта',
                titleTemplate: this.$store.state.options.titleTemplate
            }
        },
        name: "Offer",
        mounted () {
            this.$store.dispatch('content/getPageContent', {pageContentID: 4021});
            ym(39511350, 'hit', this.$route.path, {title: 'Публичная оферта | ZemSkidki.ru'});
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
            content() {
                return this.$store.getters['content/getPageContent'](4021);
            },
        },
        methods: {
            closePublicOffer() {
                this.$store.commit('popups/close', {name: this.popups.publicOffer.name});
            },
        }
    }

</script>
