<template>
    <ul :class="{'item-inner-list':isInner,'coupons-menu-list':!isInner}" id="catalog-sections-tree">
        <li v-for="section in sections" :class="{'active': $route.params.sid == section.id, 'unactive': !section.count}" v-if="section.count">
            <a  class="cm-item hf"
                :class="{'open': section.show}"
                href="javascript:void(0);"
                @click="toggleSection(section.id)"
                v-if="section.sections.size > 0"><div class="left-icon" :class="{'dropdown': section.show}"><span class="i icon-list-arrow">
                  <svg class="icon">
                    <use xlink:href="@/assets/img/mob-sprite.svg#list-arrow"></use>
                  </svg></span>
            </div>
                <div class="right">
                    <div class="cm-txt">{{ section.name }}</div>
                    <div class="list-count">{{ section.count }}</div>
                </div></a>
            <router-link v-else
                        :tag="!section.count ? 'span':'a'" :to="{ name: 'catalogList', params: { sid: section.id }}"
                         class="cm-item hf"
                         @click.native="activeSection(section.id)"
                         active-class="active" >
                <div class="right">
                    <div class="cm-txt">
                        {{ section.name }}
                        <!-- <div class="right-icon">
                            <span class="i icon-list-arrow">
                              <svg class="icon">
                                <use xlink:href="@/assets/img/mob-sprite.svg#list-arrow"></use>
                              </svg>
                            </span>
                        </div> -->
                    </div>
                    <div class="list-count">{{ section.count }}</div>
                </div>
            </router-link>

            <catalog-sections-tree is-inner="true" v-show="section.show" v-if="section.sections.size > 0" :sections="section.sections"></catalog-sections-tree>
        </li>
    </ul>
</template>

<script>
    import { mapState } from 'vuex'
    import {scrollTo} from '@/helpers/animate.js';

    export default {
        name: "CatalogSectionsTree",
        props: {
            sections: {
                default: function () {
                    return []
                }
            },
            isInner: {
                default: false
            }
        },
        data: function() {
            return { }
        },
        created () {

        },
        computed: mapState({
            previousSection: state => state.actions.previousSection
        }),
        methods: {
            activeSection: function (id) {
                this.$store.commit('actions/sectionMenuClose');
                if(id && this.previousSection !== id){
                    this.$store.commit('actions/previousSection', id);
                    this.$store.dispatch('actions/activeCategory', {
                        currentSection:id,
                        isLoad: true,
                    });
                }
                if(+id === +this.$route.params.sid){
                    scrollTo('up');
                }
            },
            toggleSection: function (id) {
                if(id){
                    this.$store.commit('actions/toggleSection', {currentSection:id});
                }
            }
        }
    }
</script>

<style>
    .nav-list .nav-list {
        display: block;
    }
</style>
