/**
 * Это модуль для страницы вопросов
 * */

import axios from 'axios'
/** Модуль для формирования параметров запроса axios
 * qs.stringify({ id: data.id }),
 */
import qs from 'qs';
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

const state = {
    data: {},
    status: false,
}

// getters
const getters = {
    getList: state => id => {
        return state.data;
    },
}

const actions = {
    getList(context, data) {
        let query = {};

        axios.get(process.env.VUE_APP_API_PATH+'/public/faq/', {
            headers: jsonHeader,
            params: query,
        }).then(response => {
            if (typeof response.data === 'object' && +response.data.error === 0) {
                context.commit('setData', response.data.result);
            }
        }).catch(function (error) {
        });
    },
    sendQuest(context, data) {
        return new Promise((resolve, reject) => {
            let query = {quest: data.quest};

            axios.get(process.env.VUE_APP_API_PATH+'/public/faq/', {
                headers: jsonHeader,
                params: query,
            }).then(response => {
                resolve();
                if (typeof response.data === 'object' && +response.data.error === 0) {
                    context.commit('setStatus', response.data.error);
                }
            }).catch(function (error) {
                reject()
            });
        })
    },
}

const mutations = {
    setData(state, data) {
        Vue.set(state, 'data', data.items);
    },
    setStatus(state, data) {
        Vue.set(state, 'status', !data.error);
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}