<template>
    <div>
        <slot name="header"></slot>
        <div :class="{'popup-content':isOrder}">
            <div  :class="{'inner':isOrder}">
                <div class="content-ttl" v-if="isOrder">На кого оформляем?</div>
                <div>
                    <input-styled v-model="login"
                                  name="login"
                                  :placeholder="'Телефон'"
                                  :disabled="formAuthFields.login.disabled"
                                  :isEdit="formAuthFields.login.disabled && step === 2"
                                  @edit="edit"
                                  :is-block-arrow-phone="true"
                                  @enter="enter(formAuthFields.login.name)"
                                  :mask="{mask:'+7(999)999-99-99',showMaskOnHover: false}"
                                  :isError="formAuthFields.login.error"
                                  :errorText="formAuthFields.login.errorText"
                                  @focus="removeError(formAuthFields.login.name)"
                                  @input="removeError(formAuthFields.login.name)"
                                  ref="loginInput"
                                  :type="'tel'"
                    ></input-styled>

                    <!--<div class="row-b__col elem-pos-fit">
                        <div class="text-color-grey text-size-s" :class="{error:formAuthFields.login.error}">Допускаются только мобильные номера<br>российских операторов</div>
                    </div>-->
                </div>
                <popup-info-block :show="formAuthFields.login.error"
                       :add-class="''"
                       :title="''"
                       :modal-type="''"
                       :text="''"
                       :name="formAuthFields.login.error">
                       <div class="modal-txt">
                           <svg class="icon">
                               <use xlink:href="@/assets/img/mob-sprite.svg#emoji-banned"></use>
                           </svg>
                         <p class="txt">Допускаются только мобильные<br>номера российских операторов</p>
                       </div>
                </popup-info-block>
                <popup-info-block :show="popups.incorrectCode.show"
                       :add-class="'incorrect-code'"
                       :title="''"
                       :modal-type="''"
                       :text="''"
                       :name="popups.incorrectCode.name">
                       <div class="modal-ttl red">
                         Неправильный код
                       </div>
                       <div class="modal-txt">
                         <p class="txt">Попробуйте ещё раз<br>или запросите новый</p>
                       </div>
                </popup-info-block>
                <div style="position: relative;" v-if="step === 2">
                    <input-styled v-model="password"
                                  name="password"
                                  :type="'number'"
                                  class="mt40"
                                  :placeholder="'Код из СМС'"
                                  :isError="formAuthFields.password.error"
                                  :errorText="''"
                                  @focus="removeError(formAuthFields.password.name)"
                                  @enter="enter(formAuthFields.password.name)"
                                  @input="removeError(formAuthFields.password.name)"
                                  ref="passwordInput"
                    ></input-styled>

                    <div class="inp-s-request-timer" v-if="timer.smsBan">Повторный запрос через <b>{{ timer.timeBan }}</b> ч.</div>
                    <div class="inp-s-request-new"
                         @click="newPassword"
                         v-else-if="timer.time === 0 && !formAuthFields.password.error"><span class="link-grey hover">Запросить новый</span></div>
                    <div class="inp-s-request-timer" v-else-if="!formAuthFields.password.error">Повторный запрос через <b>{{ timer.time }} сек.</b></div>
                </div>


                <div class="button-block">
                    <button class="btn" @click="authorize">Продолжить</button>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import InputStyled from '@/components/forms/InputStyled.vue'
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue';
    import { mapState } from 'vuex'

    export default {
        name: "Auth",
        props: {
            showHeader: {
                default(){
                    return true
                }
            },
            isOrder: {
                default(){
                    return false
                }
            },
        },
        components: {
            InputStyled,
            PopupInfoBlock,
        },
        computed: {
            codeError(){
                return this.formAuthFields.password.error;
            },
            login: {
                get () {
                    return this.formAuthFields.login.value
                },
                set (value) {
                    this.$store.commit('auth/setFormAuthField', {
                        name: this.formAuthFields.login.name,
                        value:value,
                    })
                }
            },
            password: {
                get () {
                    return this.formAuthFields.password.value
                },
                set (value) {
                    this.$store.commit('auth/setFormAuthField', {
                        name: this.formAuthFields.password.name,
                        value:value,
                    })
                }
            },
            ...mapState({
                popups: state => state.popups.items,
                formAuthFields: state => state.auth.forms.auth.fields,
                timer: state => state.auth.forms.auth.timer,
                step: state => state.auth.forms.auth.step,
                isRedirect: state => state.auth.isRedirect,
            }),
        },
        watch: {
          codeError(after,before){
            if(after){
              this.$store.commit('popups/open', {name: this.popups.incorrectCode.name});
            }
          }
        },
        mounted() {
          let self = this;
          setTimeout(function(){
            self.inputFocus('loginInput')
          },150);
        },
        methods: {
            edit () {
                this.$store.commit('auth/setStep',{ step:1 })
                this.$store.commit('auth/setFormAuthDisabled',{
                    name: this.formAuthFields.login.name,
                    value: false
                });
            },
            enter (field) {
                this.removeError(field);
                this.authorize()
            },
            newPassword () {
                //this.$store.dispatch('auth/startTimer',{ time: 15 })
                this.$store.commit('auth/setFormAuthField', {
                    name: this.formAuthFields.password.name,
                    value: '',
                });
                this.$store.dispatch('auth/checkLogin',{ resend:true });
                this.inputFocus('passwordInput')
            },
            authorize () {
                switch (this.step) {
                    case 1:
                        if(!this.checkLogin()){
                            return false
                        }
                        this.$store.commit('auth/setFormAuthDisabled',{
                            name: this.formAuthFields.login.name,
                            value: true
                        });
                        this.$store.commit('auth/setStep',{ step:2 });
                        this.$store.dispatch('auth/checkLogin',{ resend:false });
                        //this.$store.dispatch('auth/startTimer',{ time: 15 });
                        this.$nextTick(function () {
                            this.inputFocus('passwordInput')
                        });
                        break;
                    case 2:
                        if(!this.checkLogin() || !this.checkPassword()){
                            return false
                        }
                        this.$store.dispatch('auth/getToken',{isOrder:this.isOrder}).then(()=>{
                            if(this.isRedirect) {
                                this.$router.push('/personal');
                            }
                            this.$store.commit('auth/setStep',{ step:1 });
                            this.$store.commit('auth/setFormAuthDisabled',{
                                name: this.formAuthFields.login.name,
                                value: false
                            });
                        });
                        break;
                }
            },
            inputFocus(ref) {
                if(!!this.$refs[ref] ){
                  if(this.$route.name != 'catalogDetail' || this.popups.publicAuth.show){
                    this.$refs[ref].$el.getElementsByClassName('inp-s')[0].focus()
                  }
                }
            },
            checkLogin () {
                let phone = this.formAuthFields.login.value.replace(/[^\+0-9]/g, '');
                if(phone.length < 12 || phone[2] !== '9') {
                    this.$store.commit('auth/setFormAuthError',{
                        name: this.formAuthFields.login.name,
                        value: true
                    });
                    return false;
                }
                return true;
            },
            checkPassword () {
                if(this.formAuthFields.password.value.length < 4 ) {
                    this.$store.commit('auth/setFormAuthError',{
                        name: this.formAuthFields.password.name,
                        value: true
                    });
                    return false;
                }
                return true;
            },
            removeError (field) {
                this.$store.commit('auth/setFormAuthError',{ name: field, value: false })
            },
        }
    }
</script>

<style scoped>
    .h2.mb32 + .row-b {
        padding-top: 0 !important;
    }
</style>
