// Initialize Firebase
firebase.initializeApp(firebaseConfig);


window.notify_subscribe = function () {    // запрашиваем разрешение на получение уведомлений
    if (firebase.messaging.isSupported()) {
        let messaging = firebase.messaging();
        messaging.requestPermission()
            .then(function () {
                // получаем ID устройства
                messaging.getToken()
                    .then(function (currentToken) {
                        console.log(currentToken);

                        if (currentToken) {
                            window.sendTokenToServer(currentToken);
                        } else {
                            console.warn('Не удалось получить токен.');
                            window.setTokenSentToServer(false);
                        }
                    })
                    .catch(function (err) {
                        console.warn('При получении токена произошла ошибка.', err);
                        window.setTokenSentToServer(false);
                    });
            })
            .catch(function (err) {
                console.warn('Не удалось получить разрешение на показ уведомлений.', err);
            });
    }
}

// отправка ID на сервер
window.sendTokenToServer = function (currentToken) {
    if (!window.isTokenSentToServer(currentToken)) {
        console.log('Отправка токена на сервер...');

        let formData = new FormData();
        formData.append('notify_token', currentToken)

        axios.post(process.env.VUE_APP_API_PATH+'/public/notify/', formData, {
            headers: {
                'Content-Type': 'application/json;charset=utf-8',
                'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
            },
            withCredentials: true,
        });

        window.setTokenSentToServer(currentToken);
    } else {
        console.log('Токен уже отправлен на сервер.');
    }
}

// используем localStorage для отметки того,
// что пользователь уже подписался на уведомления
window.isTokenSentToServer = function (currentToken) {
    return window.localStorage.getItem('sentFirebaseMessagingToken') == currentToken;
}

window.setTokenSentToServer = function (currentToken) {
    window.localStorage.setItem(
        'sentFirebaseMessagingToken',
        currentToken ? currentToken : ''
    );
}

// браузер поддерживает уведомления
// вообще, эту проверку должна делать библиотека Firebase, но она этого не делает
if ('Notification' in window) {
    let messaging = firebase.messaging();

    // пользователь уже разрешил получение уведомлений
    // подписываем на уведомления если ещё не подписали
    if (Notification.permission.toLowerCase() === 'granted') {
        window.notify_subscribe();
    }
}
