/**
 * Это модуль для рейтинга
 * */

import axios from 'axios'
/** Модуль для формирования параметров запроса axios
 * qs.stringify({ id: data.id }),
 */
import qs from 'qs';
import {jsonHeader, wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

const state = {
    isLoadedProcess: true,
    isLastPage: false,
    statistic: {},
    coupons: [],
    providers: [],
    actions: [],
    currentProvider: {},
    sort: {
        current: {
            order: 'sum',
            by: 'desc',
        },
        order: {
            name: {
                code: 'name',
            },
            count: {
                code: 'count',
            },
            sum: {
                code: 'sum',
            },
        },
        by: {
            asc: 'asc',
            desc: 'desc',
        }
    },
    page: 1,
    status: false,
}

// getters
const getters = {
    getData: state => id => {
        return state.data;
    },
    getCoupons: state => sort => {
        return state.coupons;
    },
    getSort(context, data) {
        return state.sort.current;
    }
}

const actions = {
    getStatistic(context, data) {
        axios.get(process.env.VUE_APP_API_PATH+'/public/statistic/'+context.rootState.cities.current.id, {
            headers: jsonHeader,
            withCredentials: true,
        }).then(response => {
            context.commit('setStatistic', response.data.result.statistic);
        }).catch(function (error) {
        });
    },
    getCurrentProvider(context, data) {
        context.commit('setCurrentProvider', {});
        axios.get(process.env.VUE_APP_API_PATH+'/public/rating_providers/'+data.id, {
            headers: jsonHeader,
            withCredentials: true,
        }).then(response => {
            context.commit('setCurrentProvider', response.data.result.provider);
        }).catch(function (error) {
        });
    },
    getAllStatistic(context, data) {
        axios.get(process.env.VUE_APP_API_PATH+'/public/statistic/', {
            headers: jsonHeader,
            withCredentials: true,
        }).then(response => {
            context.commit('setStatistic', response.data.result.statistic);
        }).catch(function (error) {
        });
    },
    getData(context, data) {
        if (state.isLoadedProcess) {

            if (data.action === 'sort') {
                context.commit('resetPage');
                context.commit('setLastPage', false);
            }

            if (!state.isLastPage) {

                let query = {
                    sort: state.sort.current,
                    page: state.page,
                    city: context.rootState.cities.current.xmlId
                };

                context.commit('setLoadedProcess', false);

                axios.get(process.env.VUE_APP_API_PATH+'/public/rating/', {
                    headers: jsonHeader,
                    params: query,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    withCredentials: true,
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('incrementPage');
                        context.commit('setData', response.data.result);

                        if (response.data.result.rating.isLastPage) {
                            context.commit('setLastPage', true);
                        }
                    }

                    context.commit('setLoadedProcess', true);
                }).catch(function (error) {
                    context.commit('setLoadedProcess', true);
                });
            }
        }
    },
    getProviders(context, data) {
        if (state.isLoadedProcess) {

            if (data.action === 'sort') {
                context.commit('resetPageRating');
                context.commit('setLastPage', false);
            }

            if (!state.isLastPage) {

                let query = {
                    sort: state.sort.current,
                    page: state.page,
                    city: context.rootState.cities.current.xmlId
                };

                context.commit('setLoadedProcess', false);

                axios.get(process.env.VUE_APP_API_PATH+'/public/rating/', {
                    headers: jsonHeader,
                    params: query,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    withCredentials: true,
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setProviders', response.data.result);

                        if (response.data.result.rating.isLastPage) {
                            context.commit('setLastPage', true);
                        }
                    }

                    context.commit('setLoadedProcess', true);
                }).catch(function (error) {
                    context.commit('setLoadedProcess', true);
                });
            }
        }
    },
    getActions(context, data) {
        if (state.isLoadedProcess) {

            if (data.action === 'sort') {
                context.commit('resetPageRating');
                context.commit('setLastPage', false);
            }

            if (!state.isLastPage) {

                let query = {
                    sort: state.sort.current,
                    page: state.page,
                    pageSize: 20,
                    city: context.rootState.cities.current.xmlId
                };

                context.commit('setLoadedProcess', false);

                axios.get(process.env.VUE_APP_API_PATH+'/public/rating/', {
                    headers: jsonHeader,
                    params: query,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    withCredentials: true,
                }).then(response => {
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        context.commit('setActions', response.data.result);
                        context.commit('incrementPage');
                        if (response.data.result.rating.isLastPage) {
                            context.commit('setLastPage', true);
                        }
                    }

                    context.commit('setLoadedProcess', true);
                }).catch(function (error) {
                    context.commit('setLoadedProcess', true);
                });
            }
        }
    },
    setSort(context, data) {
        let sortDirection = 'desc';
        if(state.sort.current.order === data.order){
            sortDirection = state.sort.current.by === 'asc' ? 'desc' : 'asc';
        }
        context.commit('setSort', {order: state.sort.order[data.order].code, by: sortDirection});
    },
}

const mutations = {
    setProviders(state, data) {
        for (let ind in data.rating.providers) {
            if (data.rating.providers.hasOwnProperty(ind)) {
                state.providers.push(data.rating.providers[ind]);
            }
        }
    },
    clearProviders(state, data) {
        state.providers = [];
    },
    setActions(state, data) {
        for (let ind in data.rating.items) {
            if (data.rating.items.hasOwnProperty(ind)) {
                state.actions.push(data.rating.items[ind]);
            }
        }
    },
    clearActions(state, data) {
        state.actions = [];
    },
    setCurrentProvider(state, data) {
        Vue.set(state, 'currentProvider', data);
    },
    setStatistic(state, data) {
        data.coordinates_map = [];
        for(let index in data.coordinates) {
            let item = data.coordinates[index];
            let size = [30, 30];
            if (item.count > 9){
                size = [41, 41]
            }
            let offset = [0, 5];
            if (item.count > 9){
                offset = [5, 9]
            }
            data.coordinates_map.push({
                id: index,
                type: 'placemark', // define properties here
                coords: [item.coord.split(',')[1],item.coord.split(',')[0]], // define options here
                hint: "",
                balloon: {header: '', body: '', footer: ''},
                cluster: false,
                count: item.count,
                icon: {
                    layout: "default#imageWithContent",
                    imageHref: require('@/assets/img/baloon-stat.png'),
                    imageSize: size,
                    imageOffset: [-15, -18],
                    content: item.count,
                    contentOffset: offset,
                    contentTemplate: '<div style="width: 30px;height: 12px;text-align: center;color: #FFFFFF; font-size: 12px;font-weight: bold;font-family: prox, sans-serif, Arial;">$[properties.iconContent]</div>'
                },
            });

        }

        Vue.set(state, 'statistic', data);
    },
    setSort(state, data) {
        if(data.order === 'name' && state.sort.current.order !== 'name'){
            Vue.set(state.sort, 'current', data);
            Vue.set(state.sort.current, 'by', 'asc');
        } else {
            Vue.set(state.sort, 'current', data);
        }
    },
    incrementPage(state) {
        Vue.set(state, 'page', state.page + 1);
    },
    resetPage(state) {
        Vue.set(state, 'page', 1);
        Vue.set(state, 'coupons', []);
    },
    resetPageRating(state) {
        Vue.set(state, 'page', 1);
        Vue.set(state, 'providers', []);
        Vue.set(state, 'actions', []);
        Vue.set(state, 'isLastPage', false);
    },
    setLoadedProcess(state, bool) {
        Vue.set(state, 'isLoadedProcess', bool);
    },
    setLastPage(state, bool) {
        Vue.set(state, 'isLastPage', bool);
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}