import axios from 'axios'
import {jsonHeader} from '@/helpers/axiosHeaders'
const state = {
    query: '',
    showSearchTop: false
}

// getters
const getters = {

}

const actions = {

}

const mutations = {
    toggleSearchTop(state) {
        state.showSearchTop = !state.showSearchTop
    },
    hideSearchTop(state) {
        state.showSearchTop = false
    },
    updateQuery(state, data) {
        state.query = data
    },
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}