<template>
  <div class="push-notify">
    <img src="@/assets/img/notification.svg" alt="">
    <div class="text">
      Включите уведомления,<br>чтобы узнать о новых скидках<br>в вашем городе
    </div>
    <div class="button-block">
        <div class="not-now modal-btn"  @click="close">
            Не сейчас
        </div>
      <div class="apply modal-btn" @click="apply">
          Включить
      </div>
    </div>
  </div>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        name: "PushNotify",
        data: function() {
            return {
            }
        },
        props: {
          show: {
            default: function(){
              return false
            }
          }

        },
        computed: {
          ...mapState({
              popups: state => state.popups.items,
          }),
        },
        methods: {
          close: function () {
            this.$store.commit('popups/close', {name: this.popups.pushNotify.name});
          },
          apply: function () {
            window.notify_subscribe();
            this.close();
          }
        },
    }


</script>

<style scoped>
    .fade-popup-enter-active, .fade-popup-leave-active {
        transition: opacity .15s;
    }
    .fade-popup-enter, .fade-popup-leave-to {
        opacity: 0;
    }
</style>
