import axios from 'axios'
/**
 * Модуль для формирования параметров запроса axios
 * qs.stringify({ id: data.id }),
 */
import qs from 'qs';
import {jsonHeader,wwwFormHeader} from '@/helpers/axiosHeaders'
import VueCookies from 'vue-cookies'
import Vue from 'vue'

const state = {
    categories: new Set(),
    coupons: [],
    currentOrderCompany: false,
    isLoadNextPage: true,
    isLoad: false,
    page: 1,
    favorites: {
        items: []
    },
}

const findCategory = function (categories, id) {
    let categoryReturn = false;
    for (let category of categories) {
        if(category.id === id) {
            categoryReturn = category;
        }
        if (!categoryReturn && category.sections.size > 0) {
            let categoryFind = findCategory(category.sections, id);
            if (categoryFind) {
                categoryReturn = categoryFind;
            }
        }
        if(categoryReturn) {
            break;
        }
    }
    return categoryReturn;
}

// getters
const getters = {

}

const actions = {
    resendCoupon(context, data) {
        if(!state.isLoad){
            context.commit('setLoad', true);
            let param = {
                resend: true
            }

            axios.put(process.env.VUE_APP_API_PATH+'/user/coupons/'+data.id,{},{
                withCredentials: true,
                params: param,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            }).then(response => {
                context.commit('setLoad', false);
                if(typeof response.data === 'object' && +response.data.error === 0) {

                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    getCouponList(context, data) {
        if(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined) {
            if ((!state.isLoad && state.isLoadNextPage || data.reload)) {
                context.commit('setLoad', true);
                let param = {
                    page: state.page
                }

                axios.get(process.env.VUE_APP_API_PATH + '/user/coupons/', {
                    withCredentials: true,
                    params: param,
                    paramsSerializer: function (params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8',
                        'Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false
                    },
                }).then(response => {
                    context.commit('setLoad', false);
                    if (typeof response.data === 'object' && +response.data.error === 0) {
                        if (data !== undefined && data.currentSection) {
                            response.data.result.currentSection = data.currentSection;
                        }
                        context.commit('setCouponList', {
                            items: response.data.result.items,
                            favorites: context.rootState.actions.favorites.items
                        });
                        if (state.favorites.items.length < 10) {
                            context.commit('isLoadNextPage', false);
                        } else {
                            context.commit('isLoadNextPage', response.data.result.isLoadNextPage);
                        }

                        context.commit('incrementPage');
                    } else {
                        if (+response.data.error === 401) {
                            context.dispatch('auth/logout', {}, {root: true})
                        }
                    }
                }).catch(function (error) {

                    context.commit('setLoad', false);
                });
            }
        } else {
            context.dispatch('auth/logout',{},{root:true})
        }
    },
    initialCouponState(context) {
        context.commit('isLoadNextPage', true);
        context.commit('setPage', 1);
        context.commit('setCouponList', {items: []});
    },
    refuseCoupon(context, data) {
        if(!state.isSend){
            context.commit('setLoad', true);
            let params = {
                ...data.params
            }
            axios.put(process.env.VUE_APP_API_PATH+'/user/coupons/'+data.couponId,{},{
                withCredentials: true,
                params: params,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            }).then(response => {
                context.commit('setLoad', false);

                if(typeof response.data === 'object' && +response.data.error === 0) {
                    if(response.data.result.status) {
                        context.commit('updateCoupon', {id:data.couponId,status: response.data.result.status});
                    }
                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
                if(data.popup){
                    context.commit('popups/close', {name: data.popup},{root: true});
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
    buy(context, data) {
        return new Promise((resolve, reject) => {
            if(!state.isLoad){
                context.commit('setLoad', true);
                let params = {
                    action: data.id,
                    city: context.rootState.cities.current.xmlId,
                    pay_select: data.card,
                    isMobileSite: true,
                    comment: data.comment,
                }
                for(let f in context.rootState.auth.forms.registration.fields) {
                    let filed = context.rootState.auth.forms.registration.fields[f];
                    params[filed.name] = filed.value
                }

                axios.post(process.env.VUE_APP_API_PATH+'/user/coupons/',{},{
                    withCredentials: true,
                    params: params,
                    paramsSerializer: function(params) {
                        return qs.stringify(params, {arrayFormat: 'repeat'})
                    },
                    headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
                }).then(response => {
                    context.commit('setLoad', false);
                    context.commit('popups/close', {name: context.rootState.popups.items.couponBuy.name},{root: true});
                    if(typeof response.data === 'object' && +response.data.error === 0) {
                        if(response.data.result.user) {
                            context.commit('users/setUser',{user:response.data.result.user},{root:true});
                        }
                        if(response.data.result.redirect) {
                            window.location.href = response.data.result.redirect
                        } else if(response.data.result.status == 'ok') {
                            context.commit('setCurrentOrderCompany',response.data.result.order_company_name);
                            context.commit('popups/open', {name: context.rootState.popups.items.couponBuyOk.name},{root: true});
                        } else if(response.data.result.status == 'buy') {
                            context.commit('popups/open', {name: context.rootState.popups.items.couponAlreadyIssued.name},{root: true});
                        } else {
                            context.commit('popups/open', {name: context.rootState.popups.items.couponBuyError.name},{root: true});
                        }
                    } else {
                        if( +response.data.error===401){
                            context.dispatch('auth/logout',{},{root:true})
                            context.commit('popups/open', {name: context.rootState.popups.items.couponBuyErrorAuth.name},{root: true});
                        }else if( +response.data.error===403){
                            context.commit('popups/open', {name: context.rootState.popups.items.manyAuth.name},{root: true});
                        } 
                        else {
                            context.commit('popups/open', {name: context.rootState.popups.items.couponBuyError.name},{root: true});
                        }
                    }
                    resolve();
                }).catch(function (error) {
                    resolve();
                    context.commit('setLoad', false);
                });
            }
        });
    },
    checkPayment(context, data) {
        if(!state.isSend){
            context.commit('setLoad', true);
            let params = {
                action: data.id,
                pay_select: data.pay_select
            }
            axios.post(process.env.VUE_APP_API_PATH+'/user/coupons/',{},{
                withCredentials: true,
                params: params,
                paramsSerializer: function(params) {
                    return qs.stringify(params, {arrayFormat: 'repeat'})
                },
                headers: {'Content-Type': 'application/json;charset=utf-8','Authorization': localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined ? localStorage.getItem('token') : false},
            }).then(response => {
                context.commit('setLoad', false);
                if(typeof response.data === 'object' && +response.data.error === 0) {

                } else {
                    if( +response.data.error===401){
                        context.dispatch('auth/logout',{},{root:true})
                    }
                }
            }).catch(function (error) {
                context.commit('setLoad', false);
            });
        }
    },
}


const mutations = {
    initialCouponState(context) {
        context.commit('isLoadNextPage', true);
        context.commit('setPage', 1);
        context.commit('setCouponList', {items: []});
    },
    setCouponList(state, data) {
        if(data !== null && data.items !== null) {
            if(state.page < 2) {
                state.coupons = [];
            }
            for (let itemIndex in data.items) {
                if(data.items.hasOwnProperty(itemIndex)) {
                    let item = data.items[itemIndex];
                    if(data.favorites.indexOf(item.action.id) !== -1) {
                        item.action.favorite = true;
                    }
                    state.coupons.push(item);
                }
            }
        }
    },
    updateCoupon(state, data) {
        if(data.id) {
            for (let index in state.coupons) {
                if(state.coupons[index].id === data.id) {
                    if(data.status) {
                        Vue.set( state.coupons[index],'status',data.status);
                    }
                }
            }
        }
    },
    addFavorite(state, data) {
        for (let itemIndex in state.coupons) {
            if(data.id === state.coupons[itemIndex].id) {
                data.action.favorite = true;
                Vue.set(state.coupons, itemIndex, data);
            }
        }
    },
    removeFavorite(state, data) {
        for (let itemIndex in state.coupons) {
            if(data.id === state.coupons[itemIndex].id) {
                data.action.favorite = false;
                Vue.set(state.coupons, itemIndex, data);
            }
        }
    },
    isLoadNextPage(state, data) {
        state.isLoadNextPage = data;
    },
    setLoad(state, data) {
        state.isLoad = data;
    },
    setPage(state, data) {
        state.page = data;
    },
    incrementPage(state) {
        state.page++;
    },
    setCancelToken(state, data) {
        state.cancelToken = data;
    },
    setCurrentOrderCompany(state, data) {
        state.currentOrderCompany = data;   
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}
