import Vue from 'vue'
import Router from 'vue-router'

ym(39511350, 'init', {
    defer: true,
    clickmap:true,
    trackLinks:true,
    accurateTrackBounce:true
});

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            props: true,
            component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue'),
        },
        {
            path: '/about',
            name: 'about',
            props: true,
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import('../views/About.vue'),
        },
        {
            path: '/contacts',
            name: 'contacts',
            props: true,
            component: () => import('../views/Contacts.vue'),
        },
        {
            path: '/id:id',
            name: 'catalogDetail',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Coupons.vue'),
        },
        {
            path: '/sid:sid',
            name: 'catalogList',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Coupons.vue'),
        },
        {
            path: '/catalog',
            name: 'catalog',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Coupons.vue'),
        },
        {
            path: '/search',
            name: 'search',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Search.vue'),        },
        {
            path: '/favorites',
            name: 'favorites',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Favorites.vue'),
        },
        {
            path: '/faq',
            name: 'faq',
            component: () => import(/* webpackChunkName: "faq" */ '../views/Faq.vue'),
            props: true,
        },
        {
            path: '/cooperation',
            name: 'cooperation',
            props: true,
            component: () => import(/* webpackChunkName: "cooperation" */ '../views/NewCooperation.vue'),
        },
        {
            path: '/rating',
            name: 'rating',
            props: true,
            component: () => import(/* webpackChunkName: "rating" */ '../views/Rating.vue'),
        },
        {
            path: '/personal',
            name: 'personal',
            props: true,
            component: () => import(/* webpackChunkName: "personal" */ '../views/Personal.vue'),
        },
        {
            path: '/personal/profile',
            name: 'profile',
            props: true,
            component: () => import(/* webpackChunkName: "personal" */ '../views/Profile.vue'),
        },
        {
            path: '/deposit',
            name: 'deposit',
            props: true,
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "deposit" */ '../views/Deposit.vue'),
        },
        {
            path: '/statistic',
            name: 'statistic',
            props: true,
            component: () => import(/* webpackChunkName: "rating" */ '../views/Statistic.vue'),
        },
        {
          path: '/docs',
          name: 'docs',
          props: true,
          component: () => import(/* webpackChunkName: "rating" */ '../views/Docs.vue'),
        },
        {
            path: '/push',
            name: 'push',
            component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue'),
        },
        {
            path: '*',
            component: () => import(/* webpackChunkName: "404" */ '../views/page404.vue'),
        }
    ],
    scrollBehavior(to, from, savedPosition) {
        if (to.hash) {
            return { selector: to.hash }
        } else if (savedPosition) {
            return savedPosition;
        } else {
            return { x: 0, y: 0 }
        }
    }
});
