<template>
    <div class="coupons-menu-block"  v-touch:swipe="swipeHandler">
        <div class="coupons-menu" :class="{slide:showSectionMenu}">
            <div class="popup-header">
                <div class="popup-name">
                    Каталог скидок
                    <div class="city" @click="openCitySelect()">
                        {{currentCity.name}}
                        <svg class="edit-icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#pencil"></use>
                        </svg>
                    </div>
                </div>
                <div class="close" id="coupons-menu-close" @click="sectionMenuClose">
                    <span class="i icon-close">
                        <svg class="icon">
                            <use xlink:href="@/assets/img/mob-sprite.svg#close"></use>
                        </svg>
                    </span>
                </div>
            </div>
            <catalog-sections-tree :sections="sections"></catalog-sections-tree>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    import CatalogSectionsTree from '@/components/catalog/CatalogSectionsTree.vue'

    export default {
        name: "CatalogSections",
        components: {
            CatalogSectionsTree
        },
        data(){
            return {
            }
        },
        created () {
            this.$store.dispatch('actions/getCategoriesList', {currentSection: this.$route.params.sid})
        },
        computed: mapState({
            popups: state => state.popups.items,
            sections: state => state.actions.categories,
            showSectionMenu: state => state.actions.showSectionMenu,
            currentCity: state => state.cities.current,
        }),
        methods: {
          swipeHandler(direction){

            if(direction == 'left'){
              this.$store.commit('actions/sectionMenuClose');
            }
          },
          openCitySelect() {
              this.$store.commit('popups/open', {name: this.popups.citySelect.name});
          },
            sectionMenuOpen(){
                this.$store.commit('actions/sectionMenuOpen');
            },
            sectionMenuClose(){
                this.$store.commit('actions/sectionMenuClose');
                document.getElementById('catalog-sections-tree').scrollTop = 0;
            },
        }
    }
</script>
