<template>
    <div class="all-wrapper" id="app">
          <catalog-sections></catalog-sections>
          <top-menu></top-menu>
          <div class="h-plug"><!-- Заглушка вместо хедера --></div>
          <header class="block-header" id="block-header">
              <div class="header-inner">
                  <div class="header-top">
                      <div class="h-discount-cat" @click="sectionMenuOpen">
                          <a class="hf">
                              <svg class="icon-cat">
                                  <use xlink:href="@/assets/img/mob-sprite.svg#cat"></use>
                              </svg>
                              <span class="txt">Каталог Скидок</span>
                          </a>
                      </div>
                      <div class="h-logo">
                          <router-link to="/">
                              <img src="@/assets/img/logo.svg">
                          </router-link>
                      </div>
                      <div class="h-search" @click="openSearch">
                          <a class="hf" href="javascript:void(0);">
                              <svg>
                                  <use xlink:href="@/assets/img/mob-sprite.svg#search"></use>
                              </svg>
                          </a>
                      </div>
                      <div class="h-favourite">
                          <router-link class="hf" to="/favorites/">
                              <svg>
                                  <use xlink:href="@/assets/img/mob-sprite.svg#ticket-bold"></use>
                              </svg>
                          </router-link>
                      </div>
                      <div @click="topMenuOpen" class="h-nav" id="h-nav">
                          <a class="hf" href="javascript:void(0);">
                              <div class="dots">
                                  <div class="dot"></div>
                                  <div class="dot"></div>
                                  <div class="dot"></div>
                              </div>
                          </a>
                      </div>
                  </div>
              </div>
          </header>
          <search-top :show-search="showSearch"></search-top>
          <main id="main" class="move-right">
              <router-view/>
          </main>

          <popup-info-block :show="popups.pushNotify.show"
                 :add-class="'pushNotify'"
                 :title="''"
                 :redtitle="true"
                 :modal-type="''"
                 :text="''"
                 :btntext="'Понятно'"
                 :name="popups.pushNotify.name">
                 <push-notify></push-notify>
          </popup-info-block>

          <popup-info-block :show="popups.manyAuth.show"
                 :is-set-blank="isBanned"
                 :add-class="'manyAuth'"
                 :title="''"
                 :redtitle="true"
                 :modal-type="''"
                 :text="''"
                 :btntext="'Справедливо!'"
                 :name="popups.manyAuth.name">
                 <div class="modal-txt">
                     <svg class="icon">
                         <use xlink:href="@/assets/img/mob-sprite.svg#emoji-many-auth"></use>
                     </svg>
                     <div class="modal-ttl red">
                       Слишком много<br>попыток входа
                     </div>
                   <p class="txt">Вы заблокированы на 12 часов.<br>
                       Если у вас что-то срочное – напишите<br>
                       руководителю проекта на <a href="javascript:void(0);" @click="openMailZems(); closeManyAuth();">skidki@zems.pro</a></p>
                 </div>
          </popup-info-block>

          <popup-info-block :show="popups.userBanned.show"
                 :is-set-blank="isBanned"
                 :add-class="''"
                 :title="''"
                 :redtitle="true"
                 :modal-type="''"
                 :text="''"
                 :btntext="'Справедливо!'"
                 :name="popups.userBanned.name">
                 <div class="modal-txt">
                     <svg class="icon">
                         <use xlink:href="@/assets/img/mob-sprite.svg#emoji-stop"></use>
                     </svg>
                     <div class="modal-ttl red">
                       Пользователь #{{authPhone}}<br>заблокирован!
                     </div>
                   <p class="txt">Если это произошло по ошибке,<br>напишите руководителю проекта<br>на <a href="javascript:void(0);" @click="openMailZems(); closeUserBanned();" >skidki@zems.pro</a></p>
                 </div>
          </popup-info-block>

          <popup-info-block :show="popups.mailSent.show"
                 :is-set-blank="isBanned"
                 :add-class="''"
                 :title="''"
                 :redtitle="true"
                 :modal-type="''"
                 :text="''"
                 :btntext="'Спасибо огромное!'"
                 :name="popups.mailSent.name">
                 <div class="modal-txt">
                     <svg class="icon">
                         <use xlink:href="@/assets/img/mob-sprite.svg#emoji-mail-sent"></use>
                     </svg>
                     <div class="modal-ttl text-size-m">
                      Ваше письмо упешно отправлено<br>
                      руководителю проекта ZemSkidki.ru
                     </div>
                   <p class="txt">Мы прочитаем его в любом случае<br>и свяжемся с вами, если потребуется</p>
                 </div>
          </popup-info-block>

          <popup :show="popups.publicAuth.show"
                 :is-set-blank="isBanned"
                 :add-class="'login-signup'"
                 :title="'Вход / Регистрация'"
                 :name="popups.publicAuth.name">
              <auth v-if="!token && !isBanned"></auth>
              <registration  v-if="token && !userCheck && !isBanned"></registration>
              
          </popup>
          <popup :show="popups.mailZems.show"
                 :is-set-blank="isBanned"
                 :add-class="'mailzems'"
                 :title="'Письмо руководителю ZEMSKIDKI.RU'"
                 :name="popups.mailZems.name">
              <mailZems></mailZems>
          </popup>
          <popup :show="popups.citySelect.show"
                 :add-class="'choose-city'"
                 :title="'Изменить город'"
                 :name="popups.citySelect.name">
              <citySelect></citySelect>
          </popup>
          <popup :show="popups.publicOffer.show"
                 :add-class="'public-offer'"
                 :title="'Публичная оферта'"
                 :name="popups.publicOffer.name">
              <offer></offer>
          </popup>
          <footer class="block-footer">
              <div class="footer-inner">
                  <div class="f-logo"><router-link to="/">
                      <svg class="icon-f-logo">
                          <use xlink:href="@/assets/img/mob-sprite.svg#f-logo"></use>
                      </svg></router-link></div>
                  <div class="f-feedback f-txt"><span>Если вам предоставили скидку<br>без купона – </span><a class="hf" href="javascript:void(0)" @click="showPopup('mailZems')">сообщите нам</a></div>
                  <div class="f-copyright f-txt">© 1980-2092 Алексей Земсков</div>
                  <div class="f-contact f-txt"><router-link class="hf" to="/contacts/">Контактная информация</router-link></div>
                  <div class="f-public-offer f-txt"><a class="hf" href="javascript:void(0);" @click="openPublicOffer">Публичная оферта</a></div>
              </div>
          </footer>
          <div class="some-overlay ">

          </div>
          <many-auth v-if="isBannedPermanent"></many-auth>
    </div>
</template>




<script>
    import { mapState } from 'vuex'
    import CatalogSections from '@/components/catalog/CatalogSections.vue'
    import TopMenu from '@/components/TopMenu.vue'
    import SearchTop from '@/components/SearchTop.vue'
    import Popup from '@/components/modal/Popup.vue'
    import PopupInfoBlock from '@/components/modal/PopupInfoBlock.vue'
    import CitySelect from '@/components/CitySelect.vue'
    import Auth from '@/components/user/auth/Auth.vue'
    import MailZems from '@/components/modal/MailZems.vue'
    import Registration from '@/components/modal/Registration.vue'
    import RepaymentRepay from '@/components/modal/RepaymentRepay.vue'
    import Authorization from '@/components/modal/Authorization.vue'
    import ManyAuth from '@/components/modal/ManyAuth.vue'
    import Tooltip from '@/components/Tooltip.vue';
    import PushNotify from '@/components/PushNotify.vue';
    import InputStyled from '@/components/forms/InputStyled.vue';
    import {scrollTo} from '@/helpers/animate.js';
    import Offer from '@/views/Offer.vue';

    export default {
        components: {
            CatalogSections,
            TopMenu,
            RepaymentRepay,
            SearchTop,
            Popup,
            PopupInfoBlock,
            CitySelect,
            Offer,
            Auth,
            MailZems,
            Registration,
            Authorization,
            ManyAuth,
            Tooltip,
            PushNotify,
            InputStyled,
        },
        data(){
            return {
                searchQuery: '',
                showSearch: false,
                searchFocus: false,
            }
        },
        created() {
            this.$store.dispatch('users/getCurrent');
            this.$store.dispatch('cities/setCityCookies');
            this.$store.dispatch('actions/setFavoritesCookies');
            this.$store.dispatch('actions/setVisitedCookies');
            this.$store.dispatch('users/getCookiesWarning');
            this.$store.dispatch('cities/getCities');
            this.$store.dispatch('options/getList');
        },
        mounted() {
            let self = this;
            this.$nextTick(function () {
                this.$el.style.display = '';
                this.headerFixed();
            });

            if (window.location.pathname === '/push' || window.location.pathname === '/push/') {
                    setTimeout(function () {
                        self.$store.commit('popups/open', {name: self.popups.pushNotify.name});
                        document.cookie = "notify=Y; max-age=86400; path=/";
                    }, 5000);  // после 5 минут
            } else {
                if (!this.isNotifyGranted() && !this.isNotifyBlocked()) {
                    setTimeout(function () {
                        if (cookieHelper.readCookie('notify') === null) {
                            self.$store.commit('popups/open', {name: self.popups.pushNotify.name});
                            document.cookie = "notify=Y; max-age=86400; path=/";
                        }
                    }, 5000 * 60);  // после 5 минут
                }
            }


            /*this.$store.subscribe((mutation, state) => {
                if(mutation.type=='popups/open'){
                    if(state.popups.items.notifyPageReset.show==true){
                        this.showNotifyPageReset();
                    }
                }
            });
            */

        },
        watch: {
          '$route': function (from, to) {
            this.showSearch = false;
            setTimeout(function(){

              let header = document.getElementById('block-header');
              header.classList.remove('out');
              header.classList.remove('h-fixed');
            },100);
         }
       },
        computed: {
            userCheck() {
                if(!!this.user.firstName){
                    return true;
                }
                return false;
            },
            token(){
                return this.localStorage.token !== 'false' && this.localStorage.token !== null && this.localStorage.token !== undefined ? this.localStorage.token : false
            },
            ...mapState({
                isBannedPermanent: state => state.users.isBannedPermanent,
                popups: state => state.popups.items,
                user: state => state.users.user,
                isBanned: state => state.auth.isBanned,
                cookiesWarning: state => state.users.cookiesWarning,
                currentCity:  state => state.cities.current,
                currentCityIp:  state => state.cities.currentCityIp,
                authPhone: state => state.auth.authPhone,
            }),
        },
        methods: {

            isNotifyGranted() {
                let result = false;
                if ('Notification' in window) {
                    result = Notification.permission.toLowerCase() === 'granted';
                } else {
                    result = true;
                }

                return result;
            },
            isNotifyBlocked() {
                let result = false;
                if ('Notification' in window) {
                    result = Notification.permission.toLowerCase() === 'denied';
                } else {
                    result = true;
                }

                return result;
            },
            headerFixed(){
                let header = document.getElementById('block-header'),
                    scrollPrev = 0;
                window.addEventListener('scroll', function() {
                    let scrolled = window.pageYOffset;
                    if(scrolled > 100){
                        header.classList.add('h-fixed');
                        if ( scrolled > 100 && scrolled > scrollPrev ) {
                            header.classList.add('out');
                        } else {
                            header.classList.remove('out');
                        }
                        scrollPrev = scrolled;
                    }else if(scrolled == 0){
                        header.classList.remove('h-fixed');
                    }
                    if(scrolled < 100){
                      header.classList.remove('out');
                      header.classList.remove('h-fixed');
                    }
                });
            },
            sectionMenuOpen(){
                this.$store.commit('actions/sectionMenuOpen');
            },
            sectionMenuClose(){
                this.$store.commit('actions/sectionMenuClose');
            },
            topMenuOpen(){
                this.$store.commit('menus/topMenuOpen');
            },
            topMenuClose(){
                this.$store.commit('menus/topMenuClose');
            },
            showPopup(popup) {
                this.$store.commit('popups/open',{name:popup});
            },
            openMailZems() {
                this.$store.commit('popups/open', {name: this.popups.mailZems.name});
            },
            openPublicOffer() {
                this.$store.commit('popups/open', {name: this.popups.publicOffer.name});
            },
            closeManyAuth() {
                this.$store.commit('popups/close', {name: this.popups.manyAuth.name});
            },
            closeUserBanned() {
                this.$store.commit('popups/close', {name: this.popups.userBanned.name});
            },
            hideCookiesWarning() {
                this.$store.commit('users/hideCookiesWarning');
            },
            openAuthorization: function (setIsRedirect) {
                this.$store.commit('auth/setIsRedirect', setIsRedirect);
                this.$store.commit('popups/open', {name: this.popups.publicAuth.name});
            },
            logout() {
                this.$store.dispatch('auth/logout');
                if(this.$route.name === 'personal'){
                    this.$router.push('/');
                }
            },
            openCitySelect() {
                this.$store.commit('popups/open', {name: this.popups.citySelect.name});
            },
            checkToken(){
                if(!checkToken){
                    this.logout();
                }
            },
            showNotifyPageReset(){
                this.$refs.tooltipNotifyReset.tooltipShow();
            },
            openSearch(){
                this.showSearch = !this.showSearch;
                scrollTo('up');
            }
        }

    }
    function checkToken(){
        return localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined
    }
</script>



<style lang="sass">
    body
</style>
